import { Avatar, IconButton, Menu } from '@mui/material';
import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import ToggleButtons from './ToggleButtons';

const Legend = () => {
    const rus = useSelector(state => state.rus);
    const upcase = useSelector(state => state.upcase);
    const serif = useSelector(state => state.serif);
    const italic = useSelector(state => state.italic);

    const dispatch = useDispatch();

    const handleChangeCase = () => {
        dispatch({ type: 'toggleCase' });
    };

    const handleChangeSerif = () => {
        dispatch({ type: 'toggleSerif' });
    };

    const handleChangeItalic = () => {
        dispatch({ type: 'toggleItalic' });
    };
    const subtitles = {
        titlebtnCase: ['Upper', '', 'Lower', ''],
        titlebtnSerif: ['Serif', '', 'Sans', ''],
        titlebtnItalic: ['Normal', '', 'Italic', '']
    };

    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };

    return (
        <div className='legendbg'>
            <div className='legenbtnswrap'>
                <div className='legenbtns'><ToggleButtons titles={subtitles.titlebtnSerif} switcher={serif} handleClick={handleChangeSerif} /></div>
                <div className='legenbtns'><ToggleButtons titles={subtitles.titlebtnCase} switcher={upcase} handleClick={handleChangeCase} /></div>
                <div className='legenbtns'><ToggleButtons titles={subtitles.titlebtnItalic} switcher={italic} handleClick={handleChangeItalic} /></div>
            </div>

            <ul className='legendtextwrap'>
                <li className='legenditem'><div className="altmark">equ</div><p className='legendtext'>Equivalent signs</p></li>
                <li className='legenditem'><div className="altmark">alt</div><p className='legendtext'>Alternate forms</p></li>
                <li className='legenditem'><div className="altmark">locl</div><p className='legendtext'>Local forms</p></li>
                {/* <li className='legenditem'><div className="altmark">rep</div><p className='legendtext'>Replacement forms</p></li> */}
            </ul>
            <IconButton onClick={handleClick} size="small"
                aria-controls={open ? 'account-menu' : undefined}
                aria-haspopup="true"
                aria-expanded={open ? 'true' : undefined}
            >
                <Avatar sx={{ width: 25, height: 25, bgcolor: '#CEE2E7' }}>?</Avatar>
            </IconButton>
            <Menu
                anchorEl={anchorEl}
                id="account-menu"
                open={open}
                onClose={handleClose}
                onClick={handleClose}
                PaperProps={{
                    elevation: 0,
                    sx: {
                        overflow: 'visible', bgcolor: '#ECF3F5', filter: 'drop-shadow(0px 2px 5px rgba(0,0,0,0.32))', mt: 1.5,
                        '& .MuiAvatar-root': { width: 32, height: 32, ml: -0.5, mr: 1, },
                        '&:before': { content: '""', display: 'block', position: 'absolute', top: 0, right: 14, width: 10, height: 10, bgcolor: '#ECF3F5', transform: 'translateY(-50%) rotate(45deg)', zIndex: 3000, },
                    },
                }}
                transformOrigin={{ horizontal: 'right', vertical: 'top' }}
                anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
            >
                <div className='legendinfowrap'>
                    <p className='legendinfotext'><a href="https://www.oxfordreference.com/view/10.1093/oi/authority.20110803095405301" target='top'>Alphabet</a> elements might have alternative graphic representations that do not change sort order and meaning of words they are used in:</p>
                    <li className='legenditem'><div className="altmarkinfo"><div className="altmark">equ</div></div><div className="altmarkinfotext">Same <a href="https://www.oxfordreference.com/view/10.1093/oi/authority.20110803100323720" target='top'>phoneme</a> as the corresponding main element</div></li>

                    <li className='legenditem'><div className="altmarkinfo"><div className="altmark">alt</div></div>
                        <div className="altmarkinfotext">Alternative phoneme compared to the corresponding main element</div></li>

                    <li className='legenditem'><div className="altmarkinfo"><div className="altmark">locl</div></div>
                        <div className="altmarkinfotext">Preferred over the one commonly used in other languages</div></li>
                </div>
            </Menu >
        </div >

    );
};

export default Legend;