import React from 'react';
import Tooltip from '@mui/material/Tooltip';
import { useSelector } from 'react-redux';

function getTypes(types, totalbg) {
    let color = "";
    let mark = null;
    let feature = null;
    if (types) {
        if (types.indexOf('notrussiansign') >= 0) { color = 'notrussiansign' }
        if (types.indexOf('dialectsign') >= 0) { color = 'dialectsign' }
        if (types.indexOf('historicsign') >= 0) { color = 'oldersign' }
        if (types.indexOf('lexicsign') >= 0) { color = 'lexicosign' }
        if (types.indexOf('equivalentsign') >= 0) { mark = 'equ'; color = 'equsign' }
        if (types.indexOf('alternatesign') >= 0) { mark = 'alt'; color = 'altsign' }
        if (types.indexOf('replacementsign') >= 0) { mark = 'rep'; color = 'repsign' }
        if (types.indexOf('featuresign') >= 0) { feature = 'featuresign'; mark = 'locl'; color = 'equsign' }
    }
    if (totalbg) color = totalbg;
    return { color, mark, feature }
}

const SingleLetter = ({ oneletter, showUni, totalbg }) => {
    const serif = useSelector(state => state.serif);
    const italic = useSelector(state => state.italic);
    const locallang = useSelector(state => state.locallang);
    const types = getTypes(oneletter.types, totalbg);

    const mainStyle = () => {
        let addstyle = "";
        if (serif && !italic) addstyle = " bigletterserifitalic";
        if (!serif && !italic) addstyle = " bigletteritalic";
        if (serif && italic) addstyle = " bigletterserif";
        return "bigletter" + addstyle;
    }

    const showAltmark = () => {
        if (types.mark) return (<div className='markplace'><span className='altmark'>{types.mark}</span></div>)
        else return (<div className='markplace'>&nbsp;</div>)
    }

    const showUnicode = () => {
        return (<p className='smalltitle smallunititle'>{showUni ? oneletter.display_unicode : " "}</p>)
    }

    const addtypetitle = () => {
        switch (types.mark) {
            case 'equ': return '  equivalent sign';
            case 'alt': return '  alternate';
            case 'locl': return '  local form';
            case 'rep': return '  replacement form';
            default: return ''
        }
    }

    const letterTitle = oneletter.description;

    const showLetter = () => {
        return (showUni ?
            String.fromCharCode(`0x${oneletter.unicode}`) :
            oneletter.unicodes.map(alt => { return (String.fromCharCode(`0x${alt}`)) }))
    }

    return (
        <Tooltip title={letterTitle} followCursor>
            <div className={"letter " + types.color}>
                {showUnicode()}
                <p className={mainStyle()}><span lang={(locallang !== "CYR" && types.feature) ? locallang : ""}>{showLetter()}</span></p>
                {showAltmark()}
            </div>
        </Tooltip >
    )
};

export default SingleLetter;
